.footer{
    display: flex;
    align-items: center;
    margin: -0.5rem -3.5rem;
    position: relative;
    margin-top: 7rem;
   
}

.f-content{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin-top: 4rem;
    gap: 4rem;
    color: white;
    top: 7rem;

   

}

.f-icons{
    display: flex;
    gap: 2rem;

}


.f-content>ul{
    display: flex;
    gap: 2rem;
    margin-right: 4rem;
}


@media screen and (max-width: 480px) {

    .f-content{
        transform: scale(.5);
        
    }
}